.analystics {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    padding-bottom: 20px;
    max-height: 820px;
    position: relative;

    .heading2 {
        width: 84vw;
        height: 40px;
        margin-left: auto;
        position: relative;

        .dayBox {
            display: inline-block;
            margin-left: 50px;
            position: absolute;
            z-index: 2;

            .day {
                width: 150px;
                line-height: 26px;
                background-color: white;
                border-radius: 5px;
                text-align: center;
                font-size: 14px;
                font-weight: 500;
                color: #0075FF;
                position: relative;
                display: inline-block;
                cursor: pointer;


                span {
                    position: absolute;
                    left: 20px;
                    top: -2px;
                }
            }

            .btnBox {
                width: 150px;
                height: auto;
                padding-bottom: 10px;
                padding-Top: 5px;
                background-color: white;
                margin-top: 5px;
                border-radius: 5px;
                font-size: 12px;
                box-shadow: 1px 1px 10px rgb(211, 210, 210);
                display: none;


                .btn1 {
                    margin-top: 5px;
                    height: 35px;
                    width: 90%;
                    margin-left: 7px;
                    color: #0075FF;
                    font-size: 12px;
                    font-weight: 600;
                    text-transform: none;
                    letter-spacing: .5px;
                    border-color: #CFE5FF;
                }
            }
        }

        .calender {
            display: inline-block;
            line-height: 22px;
            width: 25px;
            background-color: white;
            border-radius: 5px;
            margin-left: 210px;
            text-align: center;
            padding-bottom: 3px;
            color: #0075FF;
        }
    }


    .top {
        width: 83.2vw;
        margin-left: auto;
        min-height: 80px;

        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;

        .data1,
        .data2,
        .data3,
        .data4 {
            width: 220px;
            height: 60px;
            background-color: white;
            margin: auto;
            margin-top: 10px;
            border-radius: 10px;
        }

        .data1,
        .data2,
        .data3 {
            position: relative;

            .box {
                width: 30px;
                height: 30px;
                background-color: #0075FF;
                position: absolute;
                top: 15px;
                right: 15px;
                border-radius: 5px;
                display: grid;
                place-content: center;
                color: white;
            }

            p {
                font-size: 12px;
                position: absolute;
                top: 5px;
                left: 10px;
                color: #797979;
                font-weight: 500;
            }

            section {
                position: absolute;
                bottom: 5px;
                left: 15px;
                font-weight: bold;
                color: #0F55A7;
            }

            span {
                position: absolute;
                bottom: 8px;
                left: 95px;
                font-size: 10px;
                color: green;
                font-weight: 500;
            }
        }

        .data4 {
            display: grid;
            place-content: center;
            font-weight: 500;
            color: #0075FF;
            font-size: 14px;
        }
    }

    .scroll {
        width: 100%;
        height: 77vh;
        max-height: 640px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

    }

    .mid {
        width: 78vw;
        height: 450px;
        background-color: white;
        margin-left: auto;
        margin-right: 2.5vw;
        margin-top: 10px;
        border-radius: 10px;
        position: relative;
        padding-top: 15px;
        padding-right: 20px;

        .headings {
            position: absolute;
            top: 10px;
            left: 20px;
            font-size: 20px;
            font-weight: bold;
            color: #0F55A7;
        }

        .per {
            position: absolute;
            top: 40px;
            left: 20px;
            font-size: 14px;
            font-weight: 500;
            color: green;
        }

        .selected {
            margin-left: auto;
            width: 150px;
            position: relative;
            z-index: 10;

            input {
                background-color: #CFE5FF;
                border: none;
                border-radius: 5px;
                width: 150px;
                line-height: 30px;
                font-size: 12px;
                text-align: end;
                padding-right: 40px;
                color: #0F55A7;
                font-weight: 600;
                cursor: pointer;
            }

            .icon {
                position: absolute;
                right: 10px;
                color: #0F55A7;
            }

            .conBox {
                width: 160px;
                border-radius: 5px;
                margin-top: 3px;
                display: none;
                background-color: white;

                .but {
                    margin-top: 5px;
                    height: 35px;
                    width: 160px;
                    color: #0075FF;
                    font-size: 12px;
                    font-weight: 600;
                    text-transform: none;
                    letter-spacing: .5px;
                    border-color: #CFE5FF;
                }
            }
        }

        .chartCon {
            width: 100%;
            position: absolute;
            bottom: 10px;
        }
    }

    .bottom {
        width: 83.2vw;
        margin-left: auto;
        margin-top: 10px;
        border-radius: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;

        .boxes1,
        .boxes2 {
            width: 87%;
            height: 550px;
            margin: auto;
            margin-top: 10px;
            background-color: white;
            border-radius: 10px;
            padding: 10px;
        }

        .boxes2 {
            position: relative;

            section {
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
                padding-left: 15px;
            }

            .btnAdd {
                position: absolute;
                top: 13px;
                right: 30px;
                width: 120px;
                padding: 2px 30px 2px 30px;
                text-transform: none;
                background-color: #0F55A7;
                color: white;
                font-weight: bold;
            }

            .heads {
                width: 100%;
                margin-top: 20px;
                padding-left: 15px;
                position: relative;

                #inp {
                    border-radius: 5px;
                    line-height: 30px;
                    border: none;
                    box-shadow: 1px 0px 10px rgb(224, 223, 223);
                    width: 200px;
                    font-size: 12px;
                    padding-left: 40px;
                }

                .serIcon {
                    position: absolute;
                    left: 24px;
                    top: 5px;
                    color: #797979;
                }

                .sorting {
                    width: 120px;
                    height: 200px;
                    position: absolute;
                    right: 20px;
                    top: 0px;

                    input {
                        width: 120px;
                        border-radius: 5px;
                        line-height: 30px;
                        border: none;
                        background-color: #CFE5FF;
                        text-align: center;
                        font-size: 13px;
                        font-weight: 500;
                        color: #0075FF;
                        cursor: pointer;
                        outline: none;
                    }

                    span {
                        position: absolute;
                        top: 1.5px;
                        right: 10px;
                        color: #0075FF;
                    }

                    div {
                        width: 100%;
                        height: auto;
                        border-radius: 10px;
                        margin-top: 5px;
                        padding: 10px;
                        padding-top: 5px;
                        box-shadow: 1px 1px 10px rgb(215, 214, 214);
                        background-color: white;
                        display: none;

                        .btn {
                            border-color: #CFE5FF;
                            color: #0075FF !important;
                            width: 100%;
                            color: white;
                            text-transform: none;
                            line-height: 20px;
                            border-radius: 8px;
                            font-size: 8px;
                            font-weight: 500;
                            letter-spacing: .3px;
                            margin-top: 5px;
                        }

                        .btn:hover {
                            background-color: #CFE5FF;
                        }

                    }
                }

            }

            .head2 {
                width: 100%;
                line-height: 40px;
                margin-top: 10px;
                display: grid;
                grid-template-columns: 1fr 100px 120px 70px;

                .d,
                .d1 {
                    font-weight: 500;
                    color: #0F55A7;
                }

                .d1 {
                    padding-left: 17px;
                }
            }

            .inner {
                width: 100%;
                height: 400px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }

        .boxes1 {
            section {
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
                padding-left: 15px;
            }

            .head {
                display: grid;
                grid-template-columns: 1fr 1fr;
                margin-top: 20px;

                .div1,
                .div2 {
                    color: #0075FF;
                    font-weight: 600;
                    font-size: 18px;
                    margin-left: 40px;
                }

                .div2 {
                    text-align: right;
                    margin-right: 40px;
                }
            }

            .inner2 {
                margin: auto;
                margin-top: 10px;
                width: 95%;
                height: 445px;
                border-top: 1px solid rgb(204, 203, 203);
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }

                .main {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    line-height: 50px;
                    border-bottom: 1px solid rgb(204, 203, 203);


                    .d1,
                    .d2 {
                        color: #797979;
                        font-weight: 600;
                        margin-left: 29px;
                    }

                    .d2 {
                        font-weight: normal;
                        text-align: right;
                        margin-right: 27px;
                        font-size: 13px;

                        span {
                            color: green;
                            font-size: 10px;
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
    }


    .popUp {
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0px;
        left: 0px;
        background: rgb(0, 0, 0, 0.5);
        z-index: 51;
        display: grid;
        place-content: center;

        .innerBox {
            width: 600px;
            height: 450px;
            background-color: white;
            border-radius: 10px;
            padding: 20px;
            position: relative;

            h5 {
                color: #0F55A7;
                font-size: 16px;
                font-weight: 600;
                font-family: Arial, Helvetica, sans-serif;
            }

            .inner5 {
                display: grid;
                grid-template-columns: 1fr 1fr;

                .d1 {
                    padding-right: 15px;
                }

                .d2 {
                    padding-left: 15px;
                }

                .form-group {
                    width: 100%;

                    input {
                        line-height: 18px;
                        margin-top: 5px;
                        font-size: 12px;

                        &::placeholder {
                            color: #C6C6C6;
                        }
                    }

                    label {
                        font-size: 14px;
                        margin-top: 10px;
                        font-weight: 500;
                        color: #797979;
                    }
                }
            }

            .butt {
                position: absolute;
                right: 20px;
                bottom: 15px;
                text-transform: none;
                width: 100px;
                line-height: 18px;
                font-weight: bold;
                color: white;
                background-color: #0F55A7;
            }

            .btn{
                position: absolute;
                top:5px;
                right:10px;
                color: #C6C6C6;
            }
        }
    }



}


@media (max-width:1280px) {
    .analystics {
        width: 1280px;
        height: 100vh;

        .heading2 {
            width: 1080px;
        }

        .top {
            width: 1051px !important;
        }

        .mid {
            width: 1008px;
            margin-right: 20px;
        }

        .bottom {
            width: 1008px;
            margin-right: 20px;

            .boxes1,
            .boxes2 {
                width: 485px;
            }

            .boxes1{
                margin-right: 0px;
            }
            .boxes2{
                margin-left: 0px;
            }

            .boxes2 {
                .btnAdd {
                    right: 15px;
                }

                .heads {
                    padding-left: 5px;


                    .serIcon {
                        left: 10px;
                    }

                    .sorting {
                        right: 5px;
                    }

                    .Area {
                        right: 130px;
                    }
                }
            }
        }
    }
}