.home {
    background-color: #0F55A7;
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 220px 1fr;
    position: relative;

    .grid1{
      width: 220px;
      background-color: white;
    }

    .heading2 {
        width: 100%;
        height: 40px;
        // margin-left: auto;
        position: relative;

        .dayBox {
            display: inline-block;
            margin-left: 39px;
            position: absolute;
            z-index: 2;

            .day {
                width: 150px;
                line-height: 26px;
                background-color: white;
                border-radius: 5px;
                text-align: center;
                font-size: 14px;
                font-weight: 500;
                color: #0075FF;
                position: relative;
                display: inline-block;
                cursor: pointer;


                span {
                    position: absolute;
                    left: 20px;
                    top: -2px;
                }
            }

            .btnBox {
                width: 150px;
                height: auto;
                padding-bottom: 10px;
                padding-Top: 5px;
                background-color: white;
                margin-top: 5px;
                border-radius: 5px;
                font-size: 12px;
                box-shadow: 1px 1px 10px rgb(211, 210, 210);
                display: none;


                .btn1 {
                    margin-top: 5px;
                    height: 35px;
                    width: 90%;
                    margin-left: 7px;
                    color: #0075FF;
                    font-size: 12px;
                    font-weight: 600;
                    text-transform: none;
                    letter-spacing: .5px;
                    border-color: #CFE5FF;
                }
            }
        }

        .calender {
            display: inline-block;
            line-height: 22px;
            width: 25px;
            background-color: white;
            border-radius: 5px;
            margin-left: 205px;
            text-align: center;
            padding-bottom: 3px;
            color: #0075FF;
        }
    }

    .top {
        width: 100%;
        min-height: 80px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;

        .data1,
        .data2,
        .data3,
        .data4 {
            width: 220px;
            height: 60px;
            background-color: white;
            margin: auto;
            margin-top: 10px;
            border-radius: 10px;
        }

        .data1,
        .data2,
        .data3 {
            position: relative;

            .box {
                width: 30px;
                height: 30px;
                background-color: #0075FF;
                position: absolute;
                top: 15px;
                right: 15px;
                border-radius: 5px;
                display: grid;
                place-content: center;
                color: white;
            }

            p {
                font-size: 12px;
                position: absolute;
                top: 5px;
                left: 10px;
                color: #797979;
                font-weight: 500;
            }

            section {
                position: absolute;
                bottom: 5px;
                left: 15px;
                font-weight: bold;
                color: #0F55A7;
            }

            span {
                position: absolute;
                bottom: 8px;
                left: 95px;
                font-size: 10px;
                color: green;
                font-weight: 500;
            }
        }

        .data4 {
            display: grid;
            place-content: center;
            font-weight: 500;
            color: #0075FF;
            font-size: 14px;
        }
    }

    .mid {
        width: 100%;
        // margin-left: auto;
        min-height: 400px;
        display: grid;
        grid-template-columns: 3fr 1fr;

        .graph {
            width: 91.3%;
            height: 380px;
            margin-right: auto;
            margin-left: 39px;
            background-color: white;
            margin-top: 10px;
            border-radius: 10px;
            position: relative;
            padding-top: 10px;
            padding-right: 20px;

            .headings {
                position: absolute;
                top: 15px;
                left: 20px;
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
            }

            .per {
                position: absolute;
                top: 50px;
                left: 20px;
                font-size: 14px;
                font-weight: 500;
                color: green;
            }

            .selected {
                margin-left: auto;
                width: 160px;
                position: relative;
                z-index: 10;

                input {
                    background-color: #CFE5FF;
                    border: none;
                    border-radius: 5px;
                    width: 160px;
                    line-height: 25px;
                    font-size: 12px;
                    text-align: end;
                    padding-right: 40px;
                    color: #0F55A7;
                    font-weight: 600;
                    cursor: pointer;
                }

                .icon {
                    position: absolute;
                    right: 10px;
                    color: #0F55A7;
                }

                .conBox {
                    width: 160px;
                    border-radius: 5px;
                    margin-top: 3px;
                    display: none;
                    background-color: white;

                    .but {
                        margin-top: 5px;
                        height: 35px;
                        width: 160px;
                        color: #0075FF;
                        font-size: 12px;
                        font-weight: 600;
                        text-transform: none;
                        letter-spacing: .5px;
                        border-color: #CFE5FF;
                    }
                }
            }

            .chartCon {
                width: 100%;
                position: absolute;
                bottom: 10px;
            }
        }

        .cities {
            width: 220px;
            height: 380px;
            background-color: white;
            margin: auto;
            margin-top: 10px;
            border-radius: 10px;
            position: relative;
            padding-top: 40px;

            .head {
                position: absolute;
                top: 10px;
                left: 15px;
                font-weight: bold;
                color: #0F55A7;
            }

            .index {
                display: grid;
                width: 90%;
                grid-template-columns: 1fr 1fr;
                font-size: 12px;
                margin-left: 15px;
                color: #0075FF;
            }

            .dataBox {
                width: 100%;
                height: 300px;
                // background-color: red;
                margin-top: 10px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }
            }

        }
    }

    .bottom {
        width: 100%;
        // margin-left: auto;
        min-height: 280px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding-right: 8px;
        padding-left: 12px;

        .box {
            width: 90%;
            height: 250px;
            margin: auto;
            margin-top: 10px;
            background-color: white;
            border-radius: 10px;
        }
    }
}

@media (max-width:1350px) {
    .home{
        .top{
            padding-left: 10px;
        }
    }
}
@media (max-width:1350px) {
    .home{
        .top{
            padding-left: 13px;
        }
        .mid{
            .graph{
                width: 92%;
            }
        }
    }
}

@media (max-width:1280px) {
    .home{
        width: 1280px;
        .grid2{
            width: 1060px !important;
        }
        .top{
            padding-left: 18px;
        }
        .mid{
            .graph{
                width: 93%;
            }
        }
        .bottom{
            padding-left: 20px;
            .box{
                width: 92.5%;
            }
        }
    }
}

