.dataArray {
    width: 100%;
    line-height: 50px;
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 102px 122px 65px;

    .d {
        color: #797979;
        font-size: 12px;

        span {
            color: #0F55A7;
            position: relative;


            .infoBox {
                width: 250px;
                position: absolute;
                right: 30px;
                top: 0px;
                background-color: white;
                box-shadow: 1px 1px 5px #c7c7c7;
                border-radius: 10px;
                padding: 10px;
                padding-bottom: 0px;
                display: none;

                .head {
                    font-size: 12px;
                    color: #474747;
                    margin-top: -17px;
                    font-weight: 500;
                }

                .dataGrid {
                    display: grid;
                    grid-template-columns: 50px 1fr;
                    color: #797979;
                    margin-top: -30px;

                    .div1,
                    .div2 {
                        div {
                            font-size: 10px;
                            height: 15px;
                        }
                    }
                }

                .btn{
                    width: 150px;
                    line-height: 4px;
                    position: relative;
                    top:5px;
                    right: -40px;
                    font-size: 9px;
                    background-color: #CFE5FF;
                    color: #0F55A7;
                }

            }

            &:hover {
                .infoBox {
                    display: block;
                }
            }

        }
    }

    .d1 {
        font-weight: 500;
        padding-left: 17px;
        color: #474747;
        font-size: 16px;
        margin-top: -10px;

        div {
            font-size: 12px;
            margin-top: -25px;
            color: #797979;
        }
    }
}