.products{
    width: 100%;
    height: auto;
    background-color: #0F55A7;
    max-height: 820px;

    .ProCon{
        width: 80vw;
        height: 91vh;
        margin-left: auto;
        background-color: white;
        margin-right: 30px;
        margin-top: 3.5vh;
        border-radius: 20px 20px 0px 0px ;
        padding:20px 0px 0px 20px;
        max-height: 680px;

        .inner{
            width: 98%;
            height: 88.4vh;
            position: relative;

            .detail{
                font-size: 25px;
                font-weight: 600;
                color:#0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }
            .add{
               width: 180px;
               height: 50px;
               margin: auto;
               margin-top: -55px;

               .btn{
                    background-color: #0F55A7 !important;
                    color: white;
                    text-transform: none;
                    line-height: 35px;
                    border-radius: 10px;

                    span{
                        margin-left: 4px;
                    }
               }
            }

            .category, .sorting{
                width: 140px;
                height: 200px;
                
                
                input{
                    width: 140px;
                    border-radius: 10px;
                    line-height: 35px;
                    border: none;
                    background-color:#0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: white;
                    cursor: pointer;
                    outline: none;
                }
                span{
                    position: absolute;
                    top:3px;
                    right: 10px;
                    color: white;
                }

                div{
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;
                    
                    .btn{
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }
                    .btn:hover{
                        background-color: #CFE5FF;
                    }
                }
            }

            .category{
                margin-left: auto;
                margin-top: -45px;
                position: relative;
                margin-right: 16vw;
            }
            .sorting{
                width: 140px;
                height: 200px;
                position: absolute;
                right: 10px;
                top:5px;
            }

            .head{
                width: 100%;
                line-height: 40px;
                margin-top: -140px ;
                display: grid;
                grid-template-columns: 1fr repeat(6, 130px);

                div{
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                }
            }

            .productBox{
                width: 100%;
                height: 74.3vh;
                max-height: 550px;
                overflow-y: scroll;
                border-top: 1px solid rgb(178, 177, 177);
                
                &::-webkit-scrollbar{
                    display: none;
                }
            }

        }
    }
}

@media (max-width:1350px) {
    .products{
        width: 1350px;

        .ProCon{
            width: 1080px;
            margin-right: 20px;

            .inner{
                .category{
                  margin-right: 200px;   
                }
            }
        }
    }
}