.cart{
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 800px;

    
    .cartCon {
        width: 80vw;
        height: 91vh;
        margin-left: auto;
        background-color: white;
        margin-right: 30px;
        margin-top: 3.5vh;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 720px;

        .inner {
            width: 98%;
            height: 87.7vh;
            position: relative;
            max-height: 690px;


            .detail {
                font-size: 25px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 5px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .datainner {

                .orderDetail {
                    width: 100%;
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    padding: 10px;
                    border-bottom: 1px solid gray;
            
                    .left {
                        .head {
                            color: #0F55A7;
                            font-weight: 500;
                        }
            
                        .data {
                            margin-left: 20px;
            
                            div {
                                font-size: 22px;
                                font-weight: 600;
                                color: #0075FF;
                            }
            
                            span,
                            p {
                                font-size: 14px;
                                color: #797979;
                                margin-top: 5px;
                            }
                        }
                    }
            
                    .right {
            
                        .head {
                            color: #0F55A7;
                            width: 100px;
                            margin-left: auto;
                            font-weight: 500;
                        }
            
                        .data {
                            padding-right: 20px;
            
                            div {
                                color: #01B574;
                                font-weight: 600;
                                font-size: 20px;
                                text-align: right;
                            }
            
                            span,
                            p {
                                font-size: 14px;
                                color: #0F55A7;
                                font-weight: 500;
                                text-align: right;
                            }
            
                            span {
                                padding-left: 84%;
                            }
            
                            p{
                                font-size: 20px;
                                font-weight: 500;
                            }
                        }
                    }
                }
            
                .head2{
                    width: 100%;
                    height: 40px;
                    margin-top: 10px;
                    margin-bottom: -20px;
                    font-size: 18px;
                    color: #0F55A7;
                    font-weight: 500;
                    position: relative;
            
                    p{
                       position: absolute;
                       left: 30px;
                       top:3px
                    }
                    div{
                       position: absolute;
                       right: 30px;
                       top:3px
                    }
                }

                .orderContainer{
                    width: 100%;
                    padding: 20px;
                    margin-top: 10px;
                    height: 52vh;
                    max-height: 440px;
                    overflow-y: scroll;
                    &::-webkit-scrollbar{
                        display: none;
                    }
                }

                .check{
                    position: absolute;
                    width: 120px;
                    height: 40px;
                    bottom: 15px;
                    right: 20px;
                    background-color: #01B574;
                    color: white;
                    font-weight: bold;
                    text-transform: none;

                }
            }
        }
    }
}

@media (max-width:1350px) {
    .cart{
        width: 1350px;

        .cartCon{
            width: 1080px;
            margin-right: 21px;
        }
    }
}