.order,
.newPage {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 820px;

    .orderCon {
        width: 80vw;
        height: 91vh;
        margin-left: auto;
        background-color: white;
        margin-right: 30px;
        margin-top: 3.5vh;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 680px;

        .inner {
            width: 98%;
            height: 87.7vh;
            position: relative;


            .detail {
                font-size: 25px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 5px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .add {
                width: 300px;
                margin: auto;
                margin-top: -50px;
                text-align: center;
                display: flex;
                justify-content: space-around;
                color: #6B8BAF;
                font-size: 14px;
                font-weight: 600;

                span {
                    padding-right: 26px;
                    border-right: 2px solid #D9D9D9;
                    cursor: pointer;

                    &:hover {
                        color: #3a5e86;
                    }
                }

                .activeLink {
                    color: #0F55A7;
                }
            }

            .sorting {
                width: 140px;
                height: 200px;
                position: absolute;
                right: 10px;
                top: 0px;


                input {
                    width: 140px;
                    border-radius: 10px;
                    line-height: 35px;
                    border: none;
                    background-color: #0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: white;
                    cursor: pointer;
                    outline: none;
                }

                span {
                    position: absolute;
                    top: 3px;
                    right: 10px;
                    color: white;
                }

                div {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }

            .approved {
                .head {
                    width: 100%;
                    line-height: 40px;
                    margin-top: 25px;
                    display: grid;
                    grid-template-columns: 1fr 1fr 160px 160px;
                    color: #0F55A7;

                    div {
                        font-weight: 500;
                    }

                    .d1 {
                        padding-left: 5px;
                    }

                    .d2 {
                        padding-left: 15%;
                    }

                    .d3,
                    .d4 {
                        text-align: center;
                    }
                }

                .productBox {
                    width: 100%;
                    height: 74.2vh;
                    max-height: 560px;
                    margin-top: 10px;
                    overflow-y: scroll;
                    border-top: 1px solid rgb(227, 226, 226);


                    &::-webkit-scrollbar {
                        display: none;
                    }
                }
            }



            .footer {
                position: absolute;
                bottom: 0px;
                right: 0px;

                div {
                    width: 100%;
                    height: 50px;
                    position: relative;

                    .btn1,
                    .btn2 {
                        position: absolute;
                        top: 0px;
                        padding: 5px 30px 5px 30px;
                        color: white;
                        font-weight: 700;
                        text-transform: none;
                        width: 140px;
                    }

                    .btn1 {
                        right: 2vw;
                        background-color: #01B574;

                    }

                    .btn2 {
                        right: 13vw;
                        background-color: #E31A1A;
                    }
                }
            }


        }
    }
}


//////////////////////////////////////////////////////////////////////////////////

.newRequestCon {
    width: 100%;
    height: 80vh;
    max-height: 575px;
    overflow-y: scroll;
    margin-top: 30px;

    &::-webkit-scrollbar {
        display: none;
    }
}


.new {

    .orderDetail {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding: 10px;
        border-bottom: 1px solid gray;

        .left {
            .head {
                color: #0F55A7;
                font-weight: 500;
            }

            .data {
                margin-left: 20px;

                div {
                    font-size: 22px;
                    font-weight: 600;
                    color: #0075FF;
                }

                span,
                p {
                    font-size: 14px;
                    color: #797979;
                    margin-top: 5px;
                }
            }
        }

        .right {

            .head {
                color: #0F55A7;
                width: 100px;
                margin-left: auto;
                font-weight: 500;
            }

            .data {
                padding-right: 20px;

                div {
                    color: #01B574;
                    font-weight: 600;
                    font-size: 20px;
                    text-align: right;
                }

                span,
                p {
                    font-size: 14px;
                    color: #0F55A7;
                    font-weight: 500;
                    text-align: right;
                }

                span {
                    padding-left: 84%;
                }

                p {
                    font-size: 20px;
                    font-weight: 500;
                }
            }
        }
    }

    .head2 {
        width: 100%;
        height: 40px;
        margin-top: 10px;
        margin-bottom: -20px;
        font-size: 18px;
        color: #0F55A7;
        font-weight: 500;
        position: relative;

        p {
            position: absolute;
            left: 30px;
            top: 3px
        }

        div {
            position: absolute;
            right: 30px;
            top: 3px
        }
    }

    .orderContainer {
        margin-top: 10px;
        width: 100%;
        height: 53vh;
        overflow-y: scroll;
        &::-webkit-scrollbar{
            display: none;
        }
    }

}


//////////////////////////////////////////////////////////////////////////////

@media (max-width:1350px) {
    .order {
        width: 1350px;
        height: auto;

        .orderCon {
            width: 1080px;
            margin-right: 20px;
        }
    }

    .newPage{
        width: 1350px;
        height: auto;

        .orderCon{
            width: 1080px;
            margin-right: 20px;

            .inner{
                max-height: 650px;

                .footer div{
                    .btn1{
                        right: 20px;
                    }
                    .btn2{
                        right: 180px;
                    }
                }
            }
        }
    }
    .new{
        .orderContainer{
            height: 400px;
        }
    }
}