.analystics {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    padding-bottom: 20px;
    max-height: 820px;
    position: relative;

    .heading2 {
        width: 84vw;
        height: 40px;
        margin-left: auto;
        position: relative;

        .dayBox {
            display: inline-block;
            margin-left: 50px;
            position: absolute;
            z-index: 2;

            .day {
                width: 150px;
                line-height: 26px;
                background-color: white;
                border-radius: 5px;
                text-align: center;
                font-size: 14px;
                font-weight: 500;
                color: #0075FF;
                position: relative;
                display: inline-block;
                cursor: pointer;


                span {
                    position: absolute;
                    left: 20px;
                    top: -2px;
                }
            }

            .btnBox {
                width: 150px;
                height: auto;
                padding-bottom: 10px;
                padding-Top: 5px;
                background-color: white;
                margin-top: 5px;
                border-radius: 5px;
                font-size: 12px;
                box-shadow: 1px 1px 10px rgb(211, 210, 210);
                display: none;


                .btn1 {
                    margin-top: 5px;
                    height: 35px;
                    width: 90%;
                    margin-left: 7px;
                    color: #0075FF;
                    font-size: 12px;
                    font-weight: 600;
                    text-transform: none;
                    letter-spacing: .5px;
                    border-color: #CFE5FF;
                }
            }
        }

        .calender {
            display: inline-block;
            line-height: 22px;
            width: 25px;
            background-color: white;
            border-radius: 5px;
            margin-left: 210px;
            text-align: center;
            padding-bottom: 3px;
            color: #0075FF;
        }
    }


    .top {
        width: 83.2vw;
        margin-left: auto;
        min-height: 80px;

        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;

        .data1,
        .data2,
        .data3,
        .data4 {
            width: 220px;
            height: 60px;
            background-color: white;
            margin: auto;
            margin-top: 10px;
            border-radius: 10px;
        }

        .data1,
        .data2,
        .data3 {
            position: relative;

            .box {
                width: 30px;
                height: 30px;
                background-color: #0075FF;
                position: absolute;
                top: 15px;
                right: 15px;
                border-radius: 5px;
                display: grid;
                place-content: center;
                color: white;
            }

            p {
                font-size: 12px;
                position: absolute;
                top: 5px;
                left: 10px;
                color: #797979;
                font-weight: 500;
            }

            section {
                position: absolute;
                bottom: 5px;
                left: 15px;
                font-weight: bold;
                color: #0F55A7;
            }

            span {
                position: absolute;
                bottom: 8px;
                left: 95px;
                font-size: 10px;
                color: green;
                font-weight: 500;
            }
        }

        .data4 {
            display: grid;
            place-content: center;
            font-weight: 500;
            color: #0075FF;
            font-size: 14px;
        }
    }

    .scroll {
        width: 100%;
        height: 77vh;
        max-height: 640px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

    }

    .mid {
        width: 78vw;
        height: 450px;
        background-color: white;
        margin-left: auto;
        margin-right: 2.5vw;
        margin-top: 10px;
        border-radius: 10px;
        position: relative;
        padding-top: 15px;
        padding-right: 20px;

        .headings {
            position: absolute;
            top: 10px;
            left: 20px;
            font-size: 20px;
            font-weight: bold;
            color: #0F55A7;
        }

        .per {
            position: absolute;
            top: 40px;
            left: 20px;
            font-size: 14px;
            font-weight: 500;
            color: green;
        }

        .selected {
            margin-left: auto;
            width: 150px;
            position: relative;
            z-index: 10;

            input {
                background-color: #CFE5FF;
                border: none;
                border-radius: 5px;
                width: 150px;
                line-height: 30px;
                font-size: 12px;
                text-align: end;
                padding-right: 40px;
                color: #0F55A7;
                font-weight: 600;
                cursor: pointer;
            }

            .icon {
                position: absolute;
                right: 10px;
                color: #0F55A7;
            }

            .conBox {
                width: 160px;
                border-radius: 5px;
                margin-top: 3px;
                display: none;
                background-color: white;

                .but {
                    margin-top: 5px;
                    height: 35px;
                    width: 160px;
                    color: #0075FF;
                    font-size: 12px;
                    font-weight: 600;
                    text-transform: none;
                    letter-spacing: .5px;
                    border-color: #CFE5FF;
                }
            }
        }

        .chartCon {
            width: 100%;
            position: absolute;
            bottom: 10px;
        }
    }

    .bottom {
        width: 83.2vw;
        margin-left: auto;
        margin-top: 10px;
        border-radius: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;

        .box1,
        .box2 {
            width: 87%;
            height: 550px;
            margin: auto;
            margin-top: 10px;
            background-color: white;
            border-radius: 10px;
            padding: 10px;
        }

        .box2 {
            position: relative;

            section {
                font-size: 20px;
                font-weight: bold;
                color: #0F55A7;
                padding-left: 15px;
            }

            .btnAdd {
                position: absolute;
                top: 13px;
                right: 30px;
                width: 120px;
                padding: 2px 30px 2px 30px;
                text-transform: none;
                background-color: #0F55A7;
                color: white;
                font-weight: bold;
            }

            .heads {
                width: 100%;
                margin-top: 20px;
                padding-left: 15px;
                position: relative;

                #inp {
                    border-radius: 5px;
                    line-height: 30px;
                    border: none;
                    box-shadow: 1px 0px 10px rgb(224, 223, 223);
                    width: 200px;
                    font-size: 12px;
                    padding-left: 40px;
                }

                .serIcon {
                    position: absolute;
                    left: 24px;
                    top: 5px;
                    color: #797979;
                }

                .sorting {
                    width: 120px;
                    height: 200px;
                    position: absolute;
                    right: 20px;
                    top: 0px;

                    input {
                        width: 120px;
                        border-radius: 5px;
                        line-height: 30px;
                        border: none;
                        background-color: #CFE5FF;
                        text-align: center;
                        font-size: 13px;
                        font-weight: 500;
                        color: #0075FF;
                        cursor: pointer;
                        outline: none;
                    }

                    span {
                        position: absolute;
                        top: 1.5px;
                        right: 10px;
                        color: #0075FF;
                    }

                    div {
                        width: 100%;
                        height: auto;
                        border-radius: 10px;
                        margin-top: 5px;
                        padding: 10px;
                        padding-top: 5px;
                        box-shadow: 1px 1px 10px rgb(215, 214, 214);
                        background-color: white;
                        display: none;

                        .btn {
                            border-color: #CFE5FF;
                            color: #0075FF !important;
                            width: 100%;
                            color: white;
                            text-transform: none;
                            line-height: 20px;
                            border-radius: 8px;
                            font-size: 8px;
                            font-weight: 500;
                            letter-spacing: .3px;
                            margin-top: 5px;
                        }

                        .btn:hover {
                            background-color: #CFE5FF;
                        }

                    }
                }

            }

            .head2 {
                width: 100%;
                line-height: 40px;
                margin-top: 10px;
                display: grid;
                grid-template-columns: 1fr 100px 120px 70px;

                .d,
                .d1 {
                    font-weight: 500;
                    color: #0F55A7;
                }

                .d1 {
                    padding-left: 17px;
                }
            }

            .inner {
                width: 100%;
                height: 400px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }

    }

//////////////////////////////////////////////////////////////////////////////////////

.animations{
    width: 100vw;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.5);
    position: absolute;
    top:0px;
    left:0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .center{
        width: 300px;
        height: 300px;

        img{
            width: 100%;
            height: 100%;
            border-radius: 10px;
        }
    }
}

//////////////////////////////////////////////////////////////////////////////////////


}

////////////////////////////////////////////////////////////////////
.AddDealer{
    width: 100%;
    height: 100vh;
    max-height: 850px;
    background-color: rgb(0, 0, 0, 0.5);
    position: fixed;
    top:0px;
    left: 0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .form{
        width: 750px;
        height: 530px;
        background-color: white;
        border-radius: 15px;
        padding: 10px;
        position: relative;

        .headDealer{
            color: #0F55A7;
            font-size: 18px;
            font-weight: 700;
            padding-left: 20px;
            padding-top: 5px;

            .btn{
                position: absolute;
                top:5px;
                right: 10px;
                font-weight: 500;
                box-shadow: none;
                color: #C6C6C6;
            }
        }

        .divide{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;

            .first, .second{
    
                .form-group{
                    width: 85%;
                    margin: auto;
                    margin-top: 20px;
                    position: relative;
    
                    label{
                     margin-bottom: 5px;
                     font-size: 15px;
                     font-weight: 500;
                     color: #797979;
                    }
                    input{
                        line-height: 20px;
                        &::placeholder{
                            color:#C6C6C6;
                        }
                    }

                    .errors{
                        color: red;
                        font-size: 10px;
                        position: absolute;
                        left: 0px;
                        bottom: -17px;
                    }

                    
                    .select{
                        // background-color: red;
                        width: 80px;
                        position: absolute;
                        top:0px;
                        left: 0px;

                        label{
                            cursor: pointer;
                            position: relative;

                            span{
                                position: absolute;
                                top:-6px;
                            }
                        }

                        div{
                            padding: 5px;
                            padding-bottom: 7px;
                            background-color: white;
                            border-radius: 5px;
                            margin-top: -1px;
                            box-shadow: 1px 1px 5px #CFE5FF;

                            .btn2{
                                margin-top: 2px;
                                width: 70px;
                                font-weight: 600;
                                color: #469afa;
                                border-color: #CFE5FF;
                                height: 30px;
                                font-size: 13px;
                            }
                        }
                    }
                    
                }
            }
        }
        
        .btn1{
          position: absolute;
          bottom: 20px;
          right: 40px;
          text-transform: none;
          font-weight: bold;
          color: white;
          background-color: #0075FF;
        }
    }
}
////////////////////////////////////////////////////////////////////


@media (max-width:1280px) {
    .analystics{
        .bottom{

            .box2{
                width: 95% !important;
                margin-left: 20px !important;
            }
            .box1{
                width: 95% !important;
                margin-right: 20px !important;
            }
        }
    }
}


@media (max-width:1200px) {
    .analystics {
        width: 1200px;
        height: 100vh;

        .heading2 {
            width: 1005px;
        }

        .top {
            width: 970px !important;
        }

        .mid {
            width: 940px;
            margin-right: 20px;
        }

        .bottom {
            width: 970px;
            padding-right: 10px;

            .box1,
            .box2 {
                width: 455px;
            }

            .box2 {
                .btnAdd {
                    right: 15px;
                }

                .heads {
                    padding-left: 5px;


                    .serIcon {
                        left: 10px;
                    }

                    .sorting {
                        right: 5px;
                    }

                    .Area {
                        right: 130px;
                    }
                }
            }
        }
    }
}