.animationss{
    width: 100%;
    height: 100vh;
    max-height: 850px;
    background-color: rgb(0, 0, 0, 0.5);
    position: absolute;
    top:0px;
    left:0px;
    z-index: 50;
    display: grid;
    place-content: center;

    .center{
        width: 300px;
        height: 300px;

        img{
            width: 100%;
            height: 100%;
            border-radius: 10px;
        }
    }
}