.OrderCard{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr;
    padding: 20px;
    border-top: 1px solid rgb(227, 226, 226);

    .name{
        display:flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 13%;

        div{
            color: #5e5d5d;
            font-size: 24px;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: bold !important;
        }

        .city{
            color: #797979;
            font-size: 14px;
            font-weight: 500;
            margin-top: 5px;
        }
        .date{
            color: #797979;
            font-size: 12px;
            margin-top: 5px;
        }
    }

    .itemBox{
        .item{
            width: 100%;
            padding: 10px;
            display: grid;
            grid-template-columns: 1fr 160px 160px ;
            color: #797979;

            .main{
                display: grid;
                grid-template-columns: 90px 1fr;
                img{
                    width: 60px;
                    height: 60px;
                }
                p{
                    display: inline-block;
                    font-size: 12px;
                    padding-right: 35%;
                    margin-bottom: 0px;
                }
            }

            .m{
                 display: grid;
                 place-content: center;
            }
        }
    }
}