.profile {
    width: 100%;
    height: 100vh;
    max-height: 850px;
    background-color: #0F55A7;
    // padding-bottom: 20px;

    .personal {
        width: 75vw;
        height: 280px;
        background-color: white;
        margin-left: auto;
        margin-right: 4vw;
        margin-top: 20px;
        border-radius: 20px;

        .head {
            width: 100%;
            padding-left: 30px;
            line-height: 50px;
            padding-top: 5px;
            font-weight: 600;
            color: #0F55A7;
            font-size: 20px;
            position: relative;

            .btn {
                position: absolute;
                right: 35px;
                top: 10px;
                color: #0F55A7;
            }
        }

        .data {
            width: 100%;
            height: 230px;
            display: grid;
            grid-template-columns: 250px 1fr;

            .logo {

                img {
                    width: 150px;
                    height: 150px;
                    margin-top: 40px;
                    margin-left: 20px;
                }
            }

            .content {
                padding-top: 22px;

                span {
                    font-size: 14px;
                    color: #797979;
                    font-weight: 500;
                }

                div {
                    width: 94%;
                    line-height: 40px;
                    border: 1px solid rgb(204, 202, 202);
                    border-radius: 5px;
                    padding-left: 20px;
                    color: #666666;
                    font-weight: 600;
                    margin-top: 10px;
                    margin-bottom: 20px;
                }
            }
        }
    }

    .contact {
        width: 75vw;
        height: 350px;
        background-color: white;
        margin-left: auto;
        margin-right: 4vw;
        margin-top: 10px;
        border-radius: 20px;

        .head {
            width: 100%;
            padding-left: 30px;
            line-height: 50px;
            padding-top: 5px;
            font-weight: 600;
            color: #0F55A7;
            font-size: 20px;
        }

        .con{
            padding-left: 40px;
            margin-top: 6px;
        }

        span {
            font-size: 14px;
            color: #797979;
            font-weight: 500;
        }

        p {
            width: 95%;
            line-height: 40px;
            border: 1px solid rgb(204, 202, 202);
            border-radius: 5px;
            color: #666666;
            font-weight: 600;
            margin-top: 5px;
            margin-bottom: 20px;
            padding-left: 20px;
        }
    }

    .edit{
        width: 100%;
        height: 100vh;
        position: fixed;
        top:0px;
        left: 0px;
        background-color: rgb(0, 0, 0, 0.5);
        display: grid;
        place-content: center;

        .dataCon{
            width: 740px;
            height: 580px;
            background-color: white;
            border-radius: 20px;
            padding: 30px;
            
            div{
                font-weight: 500;
                font-size: 18px;
                width: 100%;
                position: relative;
    
                .btn {
                    position: absolute;
                    right: 0px;
                    top: -20px;
                    color: #0F55A7;
                }
            }

            label{
                font-size: 14px;
                margin-top: 15px;
                margin-bottom: 5px;
                color: #797979;
            }
            section{
                width: 95px;
                margin-left: auto;
                margin-top: 20px;
            
                .btn{
                    background-color: #0F55A7;
                    color: white;
                    padding: 2px 30px 3px 30px;
                }
            }
        }
    }
}

@media (max-width:1200px) {
    .profile{
        width: 1200px;

        .personal, .contact{
            width: 940px;
            margin-right: 20px;
        }
    }
}