
.activeQuerry{
    color: #1f344b !important;
}

.querries{
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 800px;

    .querryCon{
        width: 80vw;
        height: 91vh;
        margin-left: auto;
        background-color: white;
        max-height: 700px;
        margin-right: 30px;
        margin-top: 3.5vh;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;

        .inner {
            width: 98%;
            height: 87.7vh;
            position: relative;
            max-height: 670px;


            .detail {
                font-size: 25px;
                font-weight: 600;
                margin-top: 5px;
                margin-left: 5px;
                color: #0F55A7;
                font-family: Arial, Helvetica, sans-serif;
            }

            .add {
                width: 300px;
                margin: auto;
                margin-top: -50px;
                text-align: center;
                display: flex;
                justify-content: space-around;
                color: #6B8BAF;
                font-size: 14px;
                font-weight: 600;

                span {
                    padding-right: 26px;
                    border-right: 2px solid #D9D9D9;
                    cursor: pointer;

                    &:hover {
                        color: #3a5e86;
                    }
                }

                .activeLink {
                    color: #0F55A7;
                }
            }
            
            .sorting {
                width: 140px;
                height: 200px;
                position: absolute;
                right: 10px;
                top: 0px;
                z-index: 10;


                input {
                    width: 140px;
                    border-radius: 10px;
                    line-height: 35px;
                    border: none;
                    background-color: #0F55A7;
                    text-align: center;
                    font-size: 13px;
                    font-weight: 500;
                    color: white;
                    cursor: pointer;
                    outline: none;
                }

                span {
                    position: absolute;
                    top: 3px;
                    right: 10px;
                    color: white;
                }

                div {
                    width: 100%;
                    height: auto;
                    border-radius: 10px;
                    margin-top: 5px;
                    padding: 10px;
                    padding-top: 5px;
                    box-shadow: 1px 1px 10px rgb(215, 214, 214);
                    background-color: white;
                    display: none;

                    .btn {
                        border-color: #CFE5FF;
                        color: #0075FF !important;
                        width: 100%;
                        color: white;
                        text-transform: none;
                        line-height: 20px;
                        border-radius: 8px;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .3px;
                        margin-top: 5px;
                    }

                    .btn:hover {
                        background-color: #CFE5FF;
                    }
                }
            }
        }

    }
}

.unsolved, .solved, .deniedQuery{
    width: 100%;
    height: 81vh;
    max-height: 625px;
    margin-top: 20px;
    overflow-y: scroll;
    &::-webkit-scrollbar{
        display: none;
    }
}
.unsolved{
    // background-color: rgb(230, 243, 246);
    padding-top: 10px;
}
.solved{
    background-color: rgb(176, 227, 203);
}
.deniedQuery{
    background-color: rgb(175, 175, 231);
}


@media (max-width:1350px) {
    .querries{
        width: 1350px;
        height: auto;

        .querryCon{
            width: 1060px;
        }
    }
}