.setting{
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 750px;
    position: relative;

    .box{
        width: 75%;
        margin-left: auto;
        background-color: white;
        margin-right: 60px;
        margin-top: 20px;
        border-radius: 20px;
        padding: 20px;
        padding-bottom: 30px;

        h5{
            font-weight: bold;
            color: #0F55A7;
        }

        .mail, .pass{
            width: 90%;
            margin: auto;
            border-top: 1px solid rgb(197, 196, 196);
            border-bottom: 1px solid rgb(197, 196, 196);
            margin-top: 30px;
            line-height: 60px;
            color: #797979;
            padding-left: 30px;
            font-weight: 600;
            font-family: 'Roboto';
            letter-spacing: .5px;
            word-spacing: 4px;
        }
        .pass{
            margin-top: 0px;
            border-top: none;
        }

        .plan{
            margin-top: 30px;
        }

        .plans{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            margin-top: 25px;

            .minBox{
                width: 80%;
                margin: auto;
                height: 45vh;
                max-height: 340px;
                background-color: #0F55A7;
                border-radius: 10px;
                color: white;
                padding: 10px;
                padding-left: 0px;
                position: relative;

                p{
                    width: 50%;
                    line-height: 40px;
                    text-align: center;
                    margin: auto;
                    font-size: 18px;
                    font-weight: 700;
                    letter-spacing: .5px;
                    border-bottom: 1px solid white;
                }

                ul{
                    margin-top: 30px;
                    
                    li{
                      list-style: none;
                      font-size: 14px;
                      margin-top: 5px;
                      position: relative;
                      padding-left: 30px;
                      margin-left: -15px;

                      #icon{
                        width: 18px !important;
                        height: 18px !important;
                        background-color: white;
                        position: absolute;
                        left: 0px;
                        border-radius: 50%;
                        top:2px;
                        display: grid;
                        place-content: center;
                        color: green;
                        font-weight: bold;
                      }
                    }
                }
                .btnBox{
                     width: 100%;
                     display: grid;
                     place-content: center;
                     position: absolute;
                     bottom: 20px;
                     left: 0px;
                    .btn5{
                      font-size: 13px;
                      text-transform: none;
                      background-color: white;
                      color: green;
                      font-weight: bold;
                    }
                }
            }
        }

    }
}

@media (max-width:1200px) {
    .setting{
        width: 1200px;

        .box{
            width: 78%;
            margin-right: 20px;
        }
    }
}