.item1 {
    width: 100%;
    height: 100vh;
    background-color: #0F55A7;
    max-height: 760px;


    .itemCon {
        width: 80vw;
        height: 91vh;
        margin-left: auto;
        background-color: white;
        margin-right: 30px;
        margin-top: 3.5vh;
        border-radius: 20px 20px 0px 0px;
        padding: 20px 0px 0px 20px;
        max-height: 690px;

        .inner {
            width: 98%;
            height: 88.4vh;
            max-height: 655px;
            // background-color: rgb(242, 205, 205);
            display: grid;
            grid-template-columns: 1fr 1fr;

            .imgSection {
                // background-color: aquamarine;

                .box {
                    margin: auto;
                    margin-top: 20px;
                    width: 90%;
                    height: 520px;
                    border-radius: 10px;
                    border: 1px dotted #C6C6C6;

                    .imgBox {
                        width: 300px;
                        margin: auto;
                        padding-top: 30px;

                        img {
                            width: 300px;
                            max-height: 280px;
                        }
                    }

                    .selector {
                        width: 450px;
                        margin: auto;
                        // background-color: blue;
                        margin-top: 90px;
                        overflow-x: scroll;
                        white-space: nowrap;

                        &::-webkit-scrollbar {
                            display: none;
                        }

                        div {
                            border-radius: 5px;
                            border: 1px dotted #C6C6C6;
                            margin-left: 5px;
                            margin-right: 5px;
                            display: inline-block;

                            img {
                                width: 77px;
                                height: 77px;
                                border-radius: 5px;

                            }
                        }
                    }
                }

                .add{
                    width: 200px;
                    height: 30px;
                    margin-top: 30px;
                    margin-left: auto;
                    margin-right: 25px;
                    text-align: right;
                    font-size: 16px;
                    font-weight: 500;
                    color: #a2a1a1;

                    .inc{
                       height: 25px;
                       min-width: 30px !important;
                       padding: 0px;
                       margin-left: 10px;
                       margin-top: -5px;
                       border-radius: 2px;
                       background-color: #01B574;
                       color: white;
                    }
                }
                .sub{
                    margin-top: 0px !important;
                    .inc{
                       background-color: red;
                    }
                }
            }

            .contentSection {
                padding-left: 10px;
                position: relative;

                h2 {
                    margin-top: 12px;
                }

                .top {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    margin-top: 20px;

                    .con1 {
                        .d1 {
                            color: #797979;
                            font-weight: 500;
                            margin-bottom: 20px;
                        }
                        span{
                            color: #797979;
                            font-weight: 600;
                        }
                        section{
                           color: #6B8BAF;
                           font-weight: 600;
                           margin-top: 20px;
                        }
                        p{
                            margin-top: 15px;
                            font-weight: 600;
                            color: #797979;
                            div{
                                display: inline-block;
                                font-size: 25px;
                                margin-left: 10px;
                                color: #0075FF;
                            }
                        }
                    }

                    .con2{
                        div{
                            width: 200px;
                            height: 250px;
                            background-color: #CFE5FF;
                            border-radius: 10px;
                            margin: auto;
                            margin-top: -10px;

                            .p2{
                                width: 100px;
                                line-height: 30px;
                                font-size: 14px;
                                margin: auto;
                                text-align: center;
                                border-bottom: 1px solid #474747;
                                font-weight: 500;
                                color: #474747;
                            }

                            .p3{
                                font-size: 10px;
                                margin-top: 10px;
                                padding-left: 10px;
                                padding-right: 10px;
                            }
                        }
                    }
                }

                .describe{
                 color: #797979;
                 font-size: 14px;
                 padding-right: 50px;

                 p{
                    font-weight: bold;
                    font-size: 16px;
                    margin-bottom: 10px;
                 }
                }

                .foot{
                    width: 100%;
                    height: 60px;
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    position: absolute;
                    bottom: 20px;

                    div{

                        .btn1{
                            width: 160px;
                            height: 42px;
                            text-transform: none;
                            font-weight: bolder;
                            color: white;
                            background-color: #01B574;
                            font-size: 16px;

                            span{
                                margin-left: 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width:1350px) {
    .item1{
        width: 1350px;

        .itemCon{
            width: 1070px;
            margin-right: 21x;

            .inner{
                .contentSection{
                    .top{
                        .con2{
                            div{
                                margin-top: -5px;
                            }
                        }
                    }
                }
            }
        }
    }
}